<template>
  <div id="edirtArticle" style="height: 100%;">
    <yd-layout>
      <!---->
      <yd-navbar slot="navbar" title="" bgcolor="#09bb07" color="#fff" style="height: .9rem">

      </yd-navbar>

      <!---->
      <yd-pullrefresh :callback="loadList" ref="pullrefreshDemo">
        <yd-list theme="4">

        </yd-list>
        <yd-cell-group>
          <yd-cell-item>
            <span slot="left">{{nickName}}</span>
            <span slot="right"><img slot="img" :src="portrait" style="max-width: 58px;max-height: 58px;"></span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left"></span>
            <input slot="right" max="32" v-model="title" type="text" placeholder="">
          </yd-cell-item>
          <yd-cell-item arrow type="label">
            <span slot="left"></span>
            <select slot="right" v-model="partition">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </yd-cell-item>

          <yd-cell-item>
            <el-input
              type="textarea"
              slot="right"
              :rows="12"
              style="margin: 20px;"
              placeholder=""
              v-model="content">
            </el-input>
          </yd-cell-item>

        </yd-cell-group>
        <yd-backtop></yd-backtop>
        <div v-if="nickName&&title&&partition&&content">
          <yd-button size="large" type="primary" @click.native="submit"> </yd-button>
        </div>
        <div v-else>
          <yd-button size="large" type="disabled" disabled> </yd-button>
        </div>
      </yd-pullrefresh>

      <!---->
      <yd-tabbar slot="tabbar">
        <yd-tabbar-item title="" link="/index">
          <yd-icon name="home-outline" slot="icon"></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="" link="/partition">
          <yd-icon name="type" slot="icon"></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="" link="/message">
          <yd-icon name="order" slot="icon"></yd-icon>
          <yd-badge slot="badge" type="danger">2</yd-badge>
        </yd-tabbar-item>
        <yd-tabbar-item title="" link="/search">
          <yd-icon name="search" slot="icon"></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="" link="/write" active>
          <yd-icon name="feedback" slot="icon"></yd-icon>
        </yd-tabbar-item>
        <yd-tabbar-item title="" link="/admin">
          <yd-icon name="ucenter-outline" slot="icon"></yd-icon>
        </yd-tabbar-item>
      </yd-tabbar>
    </yd-layout>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'edirtArticle',
    data () {
      return {
        nickName: '',
        portrait: '',
        title: '',
        partition: '',
        content: '',
      }
    },
    beforeCreate () {
      this.$dialog.loading.open('');
    },
    created () {
      this.$dialog.loading.close();
    },
    mounted () {
      this.getAuthor();
      this.getArticle();
    },
    computed: {

    },
    methods: {
      //
      getAuthor() {
        axios.post('/api_write/author',{
          userId: window.localStorage.getItem('userId'),
          access_token: window.localStorage.getItem('access_token'),
        })
          .then((response)=>{
            let res = response.data.result;
            this.nickName = res.data.nickName;
            this.portrait = res.data.portrait;
          })
          .catch((error)=>{
            this.$dialog.toast({
              mes: '',
              timeout: 1500,
              icon: 'error',
            });
          });
      },

      //
      getArticle() {
        axios.post('/api_write/getArticle',{
          userId: window.localStorage.getItem('userId'),
          access_token: window.localStorage.getItem('access_token'),
          id: this.$route.query.id,
        })
          .then((response)=>{
            let res = response.data.result;
            this.title = res.data.title;
            this.partition = res.data.partition;
            this.content = res.data.content;
          })
          .catch((error)=>{
            this.$dialog.toast({
              mes: '',
              timeout: 1500,
              icon: 'error',
            });
          });
      },

      //
      submit() {
        let now = new Date();
        axios.post('/api_write/edirtArticle',{
          userId: window.localStorage.getItem('userId'),
          access_token: window.localStorage.getItem('access_token'),
          id: this.$route.query.id,
          nickName: this.nickName,
          portrait: this.portrait,
          title: this.title,
          partition: this.partition,
          post_time: now.getFullYear()+"-"+((now.getMonth()+1)<10?"0":"")+(now.getMonth()+1)+"-"+(now.getDate()<10?"0":"")+now.getDate(),
          content: this.content,
        })
          .then((response)=>{
            this.$dialog.toast({
              mes: response.data.msg,
              timeout: 1500,
              icon: 'success',
            });
            location.reload();
          })
          .catch((error)=>{
            this.$dialog.toast({
              mes: '',
              timeout: 1500,
              icon: 'error',
            });
          });
      },

      //
      loadList() {
        location.reload();
        this.$dialog.toast({
          mes: '',
        });
        this.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
      },

    },
    components:{  },
  }
</script>

<style scoped>

</style>



